export default function callApi(
  method: string,
  url: string,
  path: string,
  data?: any
) {
  return fetch(url + path, {
    method,
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    mode: 'cors',
    body: JSON.stringify(data),
  }).then(res => res.json())
}
